* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  background-color: #2c3338;
  color: #dcddde;
  font-smoothing: antialiased;
  font-size: 1.6rem;
}

img, embed, object, video {
  max-width: 100%;
}

@media only screen and (max-width: 767px) {
  .navbar.open {
    left: 0;
  }
  .navbar {
    display: flex;
    position: absolute;
    top: 0;
    left: -800px;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    background: #2c3338;
    border: 0 solid transparent;
    letter-spacing: 1px;
    z-index: 101;
    padding-top: 50px;
    height: calc(100vh);
    width: 100%;
    transition: all 0.3s linear;
  }
  .navbar > .navlabel {
    display: flex;
    background: #2c3338;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 12.5%;
    line-height: 12.5%;
    text-align: center;
    min-height: 50px;
    border-bottom: 1px solid #e4cd8a;
  }
  .navbar > .navlabel:first-child {
    border-top: 1px solid #e4cd8a;
  }
  .navbar .navlabel:hover {
    background: #424d55;
  }
  .navbar .navtext {
    font-family: "Sansita", sans-serif;
    font-size: 2rem;
    letter-spacing: 1px;
    color: #e4cd8a;
    user-select: none;
    transition: all 0.3s linear;
  }
  .navbar .navicon {
    display: none;
  }
  .hamburger {
    position: fixed;
    top: 0;
    left: 0;
    width: 5rem;
    height: 5rem;
    padding: 1rem;
    background: #2c3338;
    cursor: pointer;
    margin: 0 auto;
    z-index: 102;
  }
  .hamburger > .hamburger-panel {
    background-color: #e4cd8a;
    width: 3rem;
    height: .6rem;
    margin-bottom: 0.6rem;
    transition: all 0.3s linear;
  }
  .hamburger.open .hamburger-1 {
    transform: translateY(1.2rem) rotate(45deg);
  }
  .hamburger.open .hamburger-2 {
    opacity: 0;
  }
  .hamburger.open .hamburger-3 {
    transform: translateY(-1.2rem) rotate(-45deg);
  }
}

@media only screen and (min-width: 768px) and (max-width: 1279px) {
  .hamburger {
    display: none;
  }
  .navbar {
    display: flex;
    position: fixed;
    top: 0;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    background: #2c3338;
    border: 0 solid transparent;
    letter-spacing: 1px;
    z-index: 101;
    margin: 0;
    min-height: 50px;
    width: 100%;
    border-bottom: 1px solid #e4cd8a;
    transition: all 0.3s linear;
  }
  .navlabel {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 15px;
    margin: auto;
    transition: all 0.3s linear;
    height: 50px;
  }
  .navlabel:first-child {
    margin-left: 8%;
  }
  .navlabel:last-child {
    margin-right: 8%;
  }
  .navlabel:hover {
    background: #424d55;
  }
  .navtext {
    font-family: "Sansita", sans-serif;
    font-size: 1.6rem;
    letter-spacing: 1px;
    color: #e4cd8a;
    user-select: none;
    transition: all 0.3s linear;
    line-height: 100%;
  }
  .navicon {
    fill: #8eadB1;
  }
}

@media only screen and (min-width: 1280px) {
  .hamburger {
    display: none;
  }
  .navbar {
    display: flex;
    position: fixed;
    top: 0;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
    background: #2c3338;
    border: 0 solid transparent;
    letter-spacing: 1px;
    z-index: 101;
    margin: 0;
    min-height: 50px;
    width: 100%;
    border-bottom: 1px solid #e4cd8a;
    transition: all 0.3s linear;
  }
  .navlabel {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 30px;
    margin: auto;
    transition: all 0.3s linear;
    height: 50px;
  }
  .navlabel:first-child {
    margin-left: 8%;
  }
  .navlabel:last-child {
    margin-right: 8%;
  }
  .navlabel:hover {
    background: #424d55;
  }
  .navtext {
    font-family: "Sansita", sans-serif;
    font-size: 1.6rem;
    letter-spacing: 1px;
    color: #e4cd8a;
    user-select: none;
    transition: all 0.3s linear;
    line-height: 100%;
  }
  .navicon {
    fill: #8eadB1;
  }
}
